<template>
<vue-instagram token="5931972678.97c04ed.2885623a31394fb78c7ea18491e1d6f1" username="carterknife" :count="100" class="card-columns">
  <template slot="feeds" scope="props" v-if="props.feed.type == 'image' ">
    <a :href="props.feed.link" target="_blank">
        <div tag="div"  class="card mb-4">
            <div style="position:relative">
              <img class="card-img-bottom" :src="props.feed.images.standard_resolution.url" :alt="props.feed.caption.text">
              <i class="fa fa-play" aria-hidden="true" v-if="props.feed.type == 'video'" ></i>
            </div>
            <div class="card-footer">
              <p>{{props.feed.caption.text}}</p>
            </div>
        </div>
        </a>
  </template>
  <template slot="error" scope="props">
      <div class="fancy-alert"> {{ props.error.error_message }} </div>
  </template>
</vue-instagram>
</template>
<style scoped lang="scss">
.card-header {
    background: white;
}
.card-title {
    text-transform: capitalize;
    margin: 0;
}
.fa-play {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    color: rgba(255,255,255,.75);
    font-size: 5rem;
}
.card {
    .card-body {
        min-height: 0;
        padding: 1.25rem;
    }
    .card-footer p {
        color: initial;
        text-transform: capitalize;
        margin: 0;
    }
}
</style>
<script>
import VueInstagram from 'vue-instagram'

export default {
  name: 'App',
  components: {
    VueInstagram
  }
}
</script>



